import LocationOn from '@mui/icons-material/LocationOn'
import { Card, CardContent, Grid, Typography } from '@mui/material'
import OpacityIcon from '@mui/icons-material/Opacity';
import CardHeader from '@mui/material/CardHeader';
import AirIcon from '@mui/icons-material/Air';
import ThermostatIcon from '@mui/icons-material/Thermostat';
const CardCurrent = ({info}) => {
  return (
    <Card sx={{backgroundColor: '#001e3c', width: '90%',maxWidth: 500 }}>
        {info?.name &&(
            <>
                <CardHeader
                    title={<Typography fontWeight={700} sx={{color: 'white'}}>{`Temperatura actual ${info?.main?.temp}`}</Typography>}
                    avatar={ <ThermostatIcon fontSize='large' sx={{color: 'white'}} /> }
                    subheader={
                        <Grid item xs={12} md={7} lg={7} display='flex' flexDirection='row' sx={{color: 'white'}}>
                            <Typography fontWeight={400} sx={{opacity:0.8}}>
                                {`Min ${info?.main?.temp_min} - Max ${info?.main?.temp_max}`}
                            </Typography>
                        </Grid>
                    }
                />
                <CardContent>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={12} lg={12} display='flex' flexDirection='row' sx={{marginTop:-3,color: 'white'}}>
                            <Typography variant="body2" color="text.secondary">
                                <LocationOn sx={{color: 'white'}} />
                            </Typography>
                            <Typography fontWeight={500} sx={{marginLeft:1}}>
                            {`${info?.name}, ${new Date(info?.fecha)}`}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} sm={12} lg={12} display='flex' flexDirection='row' justifyContent='center' alignItems='center' sx={{marginTop:0,color: 'white'}}>
                            <Typography variant="body2" color="text.secondary">
                                <AirIcon sx={{color: 'white'}} />
                            </Typography>
                            <Typography fontWeight={500} sx={{marginLeft:1}}>
                                {`${info?.wind?.speed} km/h`}
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                                <OpacityIcon sx={{color: 'white',marginLeft: 5}} />
                            </Typography>
                            <Typography fontWeight={500} sx={{marginLeft:1}}>
                                {`${info?.main?.humidity}%`}
                            </Typography>
                        </Grid>
                    </Grid>
                </CardContent>
            </>
        )}
    </Card>
  )
}

export default CardCurrent