import axios from 'axios';

function location() {
    return axios.get('location');
}
function current(city = null) {
    let base = 'current'
    if(city){
        base = `current/${city}`
    }
    return axios.get(base);
}
function forecast(city = null) {
    let base = 'forecast'
    if(city){
        base = `forecast/${city}`
    }
    return axios.get(base);
}
const ClimaService = {
    location,
    current,
    forecast
};
export default ClimaService;
