import LocationOn from '@mui/icons-material/LocationOn'
import { Avatar, Card, CardContent, Grid, Typography } from '@mui/material'
import MyLocationIcon from '@mui/icons-material/MyLocation';
import LocalLibraryIcon from '@mui/icons-material/LocalLibrary';
import CardHeader from '@mui/material/CardHeader';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const CardLocation = ({info}) => {
  return (
    <Card sx={{backgroundColor: '#001e3c', width: '90%',maxWidth: 500 }}>
        {info?.ip_address &&(
            <>
                <CardHeader
                    title={<Typography fontWeight={700} sx={{color: 'white'}}>{info?.ip_address}</Typography>}
                    avatar={ <Avatar variant="rounded" src="avatar1.jpg" /> }
                    subheader={
                        <Grid item xs={12} md={7} lg={7} display='flex' flexDirection='row' sx={{color: 'white',marginLeft:-1}}>
                            <Typography variant="body2" color="text.secondary">
                                <LocationOn sx={{color: 'white',opacity:0.6}} />
                            </Typography>
                            <Typography fontWeight={400} sx={{opacity:0.6}}>
                                {`${info?.city} - ${info?.country}`}
                            </Typography>
                        </Grid>
                    }
                />
                <CardContent>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={5} lg={5} display='flex' flexDirection='row' justifyContent='center' sx={{marginTop:-3,color: 'white'}}>
                            <Typography variant="body2" color="text.secondary">
                                <MyLocationIcon sx={{color: 'white'}} />
                            </Typography>
                            <Typography fontWeight={500} sx={{marginLeft:1}}>
                                {`${info?.latitude},${info?.longitude}`}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} sm={6} lg={6} display='flex' flexDirection='row' justifyContent='center' sx={{marginTop:-3,color: 'white'}}>
                            <Typography variant="body2" color="text.secondary">
                                <LocalLibraryIcon sx={{color: 'white'}} />
                            </Typography>
                            <Typography fontWeight={500} sx={{marginLeft:1}}>
                                {`${info?.postal_code},${info?.country_code}`}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} md={12} lg={12} display='flex' flexDirection='row' justifyContent='center' sx={{marginTop:-2,color: 'white'}}>
                            <Typography variant="body2" color="text.secondary">
                                <AccessTimeIcon sx={{color: 'white'}} />
                            </Typography>
                            <Typography fontWeight={500} sx={{marginLeft:1}}>
                                {`${info?.timezone?.current_time}, ${info?.timezone?.name}`}
                            </Typography>
                        </Grid>
                    </Grid>
                </CardContent>
            </>
        )}
    </Card>
  )
}

export default CardLocation