import { Typography, Grid, Paper, InputBase, Divider } from "@mui/material"
import { useEffect, useState } from "react"
import Header from "./components/Header"
import ClimaService from "./services/clima.service";
import 'react-toastify/dist/ReactToastify.css';
import CardLocation from "./components/CardLocation";
import CardCurrent from "./components/CardCurrent";
import CardForecast from "./components/CardForecast";
import SearchIcon from '@mui/icons-material/Search';
import LoadingButton from '@mui/lab/LoadingButton';
const App = () => {
  const [searchName, setSearchName] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [infoIP, setInfoIP] = useState({});
  const [currentData, setCurrentData] = useState({});
  const [foreCastData, setForeCastData] = useState({});

  const actualizar = async() =>{
    await ClimaService.location().then((resp)=>{
      setInfoIP(resp)
    })
    await ClimaService.current().then((respc)=>{
      setCurrentData(respc)
    })
    setTimeout(async() => {
      await ClimaService.forecast().then((respf)=>{
        setForeCastData(respf)
      })
    }, 1000);
  }

  const searchCity = async() =>{
    setIsLoading(true)
    if(searchName?.length > 3){
      await Promise.all(
        await ClimaService.current(searchName).then((respc)=>{
          setCurrentData(respc)
        }).catch(()=>{
          setIsLoading(false)
        }),
        setTimeout(async() => {
          await ClimaService.forecast(searchName).then((respf)=>{
            setForeCastData(respf)
          }).catch(()=>{
            setIsLoading(false)
          })
        }, 1000)
      ).catch(()=>{
        setIsLoading(false)
      }).finally(()=>{
        setIsLoading(false)
      })
    }
  }
  useEffect(() => {
    actualizar()
  }, [])
  
  return (
    <>
      <Header />
      <Typography variant="h3" component='h3' fontWeight={700} sx={{textAlign: 'center'}}>
        Location
      </Typography>
      <Grid container sx={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
          <Grid item xs={12} md={12} lg={12} sx={{display: 'flex', justifyContent: 'center', alignItems: 'center',margin: 2}}>
            <CardLocation info={infoIP} key='location' />
          </Grid>
          <Grid item xs={11} lg={12} sx={{display: 'flex', justifyContent: 'center', alignItems: 'center',margin: 2}}>
            <Paper
              component="form"
              sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: 400 }}
            >
              <InputBase
                sx={{ ml: 1, flex: 1 }}
                value={searchName}
                placeholder="Ingrese una ciudad"
                inputProps={{ 'aria-label': 'ingrese una ciudad' }}
                onChange={(e)=>setSearchName(e.target.value)}
              />
              <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
              <LoadingButton loading={isLoading} sx={{border: 'none'}} onClick={()=>searchCity()}>
                <SearchIcon />
              </LoadingButton>
            </Paper>
          </Grid>
      </Grid>
      <Typography variant="h3" component='h3' fontWeight={700} sx={{textAlign: 'center'}}>
        Current
      </Typography>
      <Grid container sx={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
          <Grid item xs={12} md={8} lg={12} sx={{display: 'flex', justifyContent: 'center', alignItems: 'center',margin: 2}}>
            <CardCurrent info={currentData} key='current' />
          </Grid>
      </Grid>
      <Typography variant="h3" component='h3' fontWeight={700} sx={{textAlign: 'center'}}>
        Forecast
      </Typography>
      <Grid container flexDirection='row' spacing={1} sx={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
        {foreCastData && foreCastData?.list?.length > 0 &&(
          foreCastData?.list?.map((frInfo)=>(
            <Grid item xs={12} sm={4} lg={3} sx={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
              <CardForecast info={frInfo} city={foreCastData?.city} key={frInfo?.dt_txt} />
            </Grid>
          ))
        )}
      </Grid>
    </>
  )
}

export default App