import axios from 'axios';
import {toast} from 'react-toastify';

axios.defaults.baseURL = "https://pronostico-teco.herokuapp.com/v1";

axios.defaults.headers.common = {
    "Accept": "application/json"
};

axios.interceptors.response.use((response) => {
    if(response?.config?.responseType === "blob"){
        return response;
    }
    return response.data;
}, (error) => {
    if(!error.response){
        return false;
    }

    if (error.response.status === 400 || error.response.status === 404 ) {
        toast.error(error.response?.data?.msg || error.response?.data?.error ||error.response?.data?.errors[0].msg || 'Ocurro un problema.',{
            autoClose: false,
            hideProgressBar: false,
        });
        return Promise.reject(error.response.data);
    }
    if (error.response.status === 500 ){
        toast.error('Ocurro un problema. Comuniquese con el Administrador del sistema.',{
            autoClose: false,
        });
    }
    return Promise.reject(error);
});